.facts {
  padding: 24px 0;
  background-color: $red;
  @include screen(tablet) {
    padding: 43px 0;
  }
  &__wrapper {
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(laptop) {
      display: flex;
      gap: 0;
      justify-content: center;
    }
  }
  &__item {
    padding: 0 10px;
    grid-column: span 1 / span 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
    @include screen(laptop) {
      padding: 0 58px;
      align-items: flex-start;
      border-right: 2px solid $white;
    }
    &:nth-child(1) {
      padding-left: 0;
      border-right: 2px solid rgba(49, 28, 105, 0.1);
      @include screen(tablet) {
        border-color: $white;
      }
    }
    &:last-child {
      grid-column: span 2 / span 2;
      border: none;
      @include screen(laptop) {
        padding-right: 0;
        grid-column: span 1 / span 1;
      }
    }
    h2 {
      font-family: $DXRigrafEx;
      font-size: 32px;
      @include screen(tablet) {
        font-size: 82px;
        line-height: 90px;
      }
    }
    p {
      font-size: 16px;
      text-align: center;
      font-family: $Inter;
      @include screen(tablet) {
        font-size: 24px;
        text-align: left;
      }
    }
  }
}