* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  font-family: $DXRigraf;
  touch-action: manipulation;
}

html,
body {
  height: 100%;
  color: $white;

  font-family: $DXRigraf;
}

.hidden {
  display: none;
}

.index {
  display: flex;
  flex-direction: column;
  row-gap: 8px;
  border-radius: 8px;
  padding: 16px;
  border: 1px solid $gray-100;
  margin: 16px;
  h2 {
    padding-bottom: 16px;
    margin-bottom: 8px;
    width: 100%;
    border-bottom: 1px solid $gray-100;
    font-weight: 700;
    line-height: 32px;
    font-size: 24px;
    color: $black-100;
    font-family: $Inter;
  }
  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 8px;
  }
  &__link {
    display: flex;
    font-size: 14px;
    line-height: 19px;
    column-gap: 8px;
    font-family: $Inter;
    span {
      color: $black-100;
    }
    a {
      color: $blue;
    }
  }
}

.blur {
  pointer-events: none;
  filter: blur(10px);
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  width: 100%;
  height: 100%;

  background-color: $white;
}

.wrapper {
  margin: 0 auto;
  padding: 0 #{$container-padding};
  width: 100%;
  @include screen(desktop) {
    max-width: calc(#{$screen-desktop} + #{$container-padding * 2});
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}