.slogan {
  background-color: $purple-dark;
  padding: 45px 0;
  @include screen(tablet) {
    padding: 90px 0;
  }
  &__wrapper {
    h2 {
      font-size: 32px;
      font-weight: 600;
      text-align: center;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      @include screen(tablet) {
        font-size: 82px;
        text-align: left;
      }
      span {
        color: $red;
      }
    }
  }
}