.breadcrumbs {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 4px;
    a,
    span {
      display: inline-block;
      font-weight: 500;
      font-family: $Inter;
      font-size: 14px;
    }
    a {
      display: flex;
      align-items: center;
      color: $gray;
    }
    span {
      color: $red;
    }
  }
}