.button {
  background-color: $purple-dark;
  font-size: 16px;
  padding: 18px 32px;
  text-transform: uppercase;
  font-family: $Inter;
  white-space: nowrap;
  transition: background-color $transition-default;
  &:hover {
    background-color: $purple-hover;
  }
  &-pink {
    background-color: $red;
    &:hover {
      background-color: $red-hover;
    }
  }
}