.stats {
  padding: 24px 0;
  background-color: $purple-dark;
  @include screen(tablet) {
    padding: 43px 0;
  }
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 30px;
    @include screen(laptop) {
      gap: 0;
      flex-direction: row;
      justify-content: center;
    }
  }
  &__arrow {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -124px;
    @include screen(tablet) {
      top: -250px;
      left: 61px;
      transform: translateX(0);
    }
    img {
      height: 112px;
      width: 62px;
      @include screen(tablet) {
        height: 234px;
        width: 132px;
      }
    }
  }
  &__item {
    padding: 0 10px 0 0;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex-direction: column;
    @include screen(laptop) {
      padding: 0 58px;
      border-right: 2px solid $white;
    }
    &:nth-child(1) {
      padding-left: 0;
      border: none;
      @include screen(tablet) {
        border-right: 2px solid $white;
      }
    }
    &:last-child {
      border: none;
      @include screen(laptop) {
        padding-right: 0;
      }
    }
    h2 {
      font-family: $DXRigrafEx;
      font-size: 32px;
      @include screen(tablet) {
        font-size: 82px;
        line-height: 90px;
      }
      svg {
        max-height: 36px;
        max-width: 36px;
        @include screen(tablet) {
          max-height: 90px;
          max-width: 90px;
        }
      }
    }
    p {
      font-family: $Inter;
      @include screen(tablet) {
        font-size: 24px;
      }
    }
  }
}