.special-prices {
  padding: 50px 0 160px;
  position: relative;
  @include screen(tablet) {
    padding: 120px 0 187px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
    }
  }
  &__wrapper {
    row-gap: 20px;
    flex-direction: column;
    justify-content: center;
    display: flex;
    z-index: 2;
    position: relative;
    .breadcrumbs {
      position: absolute;
      top: 25px;
    }
    @include screen(tablet) {
      row-gap: 50px;
    }
    h2 {
      font-size: 32px;
      position: relative;
      font-family: $DXRigrafEx;
      text-transform: uppercase;
      line-height: 36px;
      @include screen(tablet) {
        font-size: 82px;
        line-height: 1;
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include screen(tablet) {
      max-width: 51%;
      margin-left: auto;
    }
    p {
      font-size: 14px;
      margin-top: 20px;
      font-family: $Inter;
      br {
        display: none;
        @include screen(tablet) {
          display: none;
        }
      }
      @include screen(tablet) {
        margin-top: 24px;
        font-size: 24px;
        line-height: 120%;
      }
    }
    .button {
      margin-top: 24px;
    }
  }
}