.header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  &__top {
    height: 30px;
    background-color: $purple-dark;
    @include screen(tablet) {
      height: 40px;
    }
    .header__wrapper {
      justify-content: center;
      @include screen(tablet) {
        justify-content: space-between;
      }
    }
  }
  &__bottom {
    height: 80px;
    background-color: $purple;
    @include screen(tablet) {
      height: 64px;
    }
  }
  &__top,
  &__bottom {
    display: flex;
    width: 100%;
    align-items: center;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 16px;
  }
  &__left,
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    img {
      min-width: 178px;
      height: 50px;
      @include screen(tablet) {
        min-width: 200px;
        height: 56px;
      }
    }
  }
  &__link {
    text-transform: uppercase;
    letter-spacing: 0.2px;
    position: relative;
    transition: color $transition-default;
    @include screen(laptop) {
      &:hover {
        color: $red;
        &:before {
          opacity: 1;
        }
      }
    }
    &:before {
      content: '';
      position: absolute;
      bottom: -5px;
      left: 0;
      width: 100%;
      height: 5px;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100%;
      opacity: 0;
      transition: opacity 0.3s;
    }
  }
  &__alert {
    font-family: $Inter;
    font-size: 14px;
    @include screen(tablet) {
      transform: translateX(-50%);
      left: 50%;
      position: absolute;
    }
  }
  &__phone {
    display: none;
    @include screen(tablet) {
      display: inline-block;
    }
  }
  &__nav {
    margin-left: 136px;
    align-items: center;
    column-gap: 45px;
    display: none;
    @include screen(tablet) {
      display: flex;
    }
  }
  &__buttons {
    display: none;
    align-items: center;
    column-gap: 27px;
    @include screen(tablet) {
      display: flex;
    }
  }
  &__cart {
    position: relative;
    span {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: $red;
      font-size: 9px;
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &__button {
    width: 38px;
    height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      transition: color $transition-default;
    }
    &:hover {
      svg {
        color: $red;
      }
    }
  }
  &__burger {
    @include screen(tablet) {
      display: none;
    }
    svg {
      path,
      rect {
        transition: transform $transition-default, opacity $transition-default,
          fill $transition-default;
      }
    }
    &.active {
      svg {
        path {
          &:nth-child(2) {
            transform: translateX(21px) translateY(-6px) rotate(45deg);
          }
          &:nth-child(3) {
            transform: translateX(-12px) translateY(24px) rotate(-45deg);
          }
        }
      }
    }
  }
}