.hero {
  position: relative;
  padding: 70px 0;
  .breadcrumbs {
    position: absolute;
    top: 25px;
    z-index: 2;
    display: none;
    @include screen(tablet) {
      display: flex;
    }
  }
  @include screen(tablet) {
    padding: 128px 0 207px;
  }
  &__bg {
    position: absolute;
    inset: 0;
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
  &__wrapper {
    row-gap: 20px;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    display: flex;
    z-index: 2;
    position: relative;
    .breadcrumbs {
      position: absolute;
      top: 25px;
    }
    @include screen(tablet) {
      flex-direction: row;
      column-gap: 104px;
      justify-content: flex-start;
    }
  }
  &__content {
    position: relative;
    &:before {
      content: "";
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translate(0, -50%);
      height: 163px;
      background-color: $black;
      filter: blur(100px);
    }
    p {
      font-family: $Inter;
      font-size: 14px;
      text-align: center;
      line-height: 120%;
      z-index: 2;
      position: relative;
      max-width: 272px;
      @include screen(tablet) {
        text-align: left;
        max-width: 494px;
        font-size: 24px;
      }
    }
  }
  &__buttons {
    display: flex;
    align-items: center;
    margin-top: 24px;
    row-gap: 20px;
    flex-direction: column;
    position: relative;
    z-index: 2;
    @include screen(tablet) {
      flex-direction: row;
      margin-top: 35px;
      column-gap: 14px;
    }
    .button {
      min-width: 273px;
      text-align: center;
      @include screen(tablet) {
        min-width: auto;
      }
    }
  }
}